import React,{ useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Card from '@vkontakte/vkui/dist/components/Card/Card'
import Header from '@vkontakte/vkui/dist/components/Header/Header'
import Separator from '@vkontakte/vkui/dist/components/Separator/Separator'
import Counter from '@vkontakte/vkui/dist/components/Counter/Counter'

import {PopoutWrapper, Footer} from '@vkontakte/vkui'

import * as config from '../config/config'

import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import '../css/Profile.css'
// localhost:8000/get_employee?id=id164078040
const Profile = (props) => {
  var selected =[];
  const [openPopout, setOpenPopout] = useState(true);
  const thematics = config.thematics;
  const updateSettings = () =>{
    props.setPopout(<ScreenSpinner size='large' />);
    var string_array = "";
    for (var i=0; i<selected.length; ++i) {
        string_array = string_array + '&categori[]=' + selected[i];
    }
    // https://task-today-tpuhack.herokuapp.com
    fetch(`https://task-today-tpuhack.herokuapp.com/update_settings?id=id${props.id_v}&${string_array}&url=${props.sign}`)
    		.then(response => response.json())
    		.then(result =>{
        	try{
                // props.setPopout(null);
				if (result == "0") {
                    
                    alert("Возникла ошибка, возможно идут технические работы, попробуйте позже");
                    window.location.reload();
                    
					// props.setActiveStory('Task')
				}else{
                    // alert("Возникла ошибка, возможно идут технические работы, попробуйте позже")
                    selected = [];
                    props.setPopout(null);
                    // window.location.reload();
                    // props.setActiveStory('FirstConnect')
                }
                // window.location.reload();
                // props.setActiveStory('Task')
                // console.log("GO HOME"); 
        }catch(err){ 
            // window.location.reload();
            alert("Возникла ошибка, возможно идут технические работы, попробуйте позже"); 
            window.location.reload();
            console.log(err);
        }

        }).catch(err => {
            // window.location.reload();
            alert("Возникла ошибка, возможно идут технические работы, попробуйте позже"); 
            window.location.reload();
            console.log(err)
        })
  }
  const openSettings = () =>{
    selected =  props.data.categori;
    props.setPopout(<PopoutWrapper alignY="center" alignX="center" >
    <div className="PopoutProfile">
      
    {thematics.map((category,index) => {
                if (props.data.categori.indexOf(category.name) != -1) {
                  return(<div className="CardCategorySelectedFirstConnect" id={category.name}  onClick={()=>{
                    // console.log("Click");
                    // document.getElementById("buttonGo").setAttribute('disabled',selected.length != 0);
                    chooseClick(category.name);
                }} key={index}>{category.name}</div>)
                }else{
                  return(<div className="CardCategoryUnselectedFirstConnect" id={category.name}  onClick={()=>{
                    // console.log("Click");
                    // document.getElementById("buttonGo").setAttribute('disabled',selected.length != 0);
                    chooseClick(category.name);
                }} key={index}>{category.name}</div>)
                }
                  
              })  
              }
              <div className="ButtonCloseProfile" id="buttonGo" onClick={()=>{
                if (selected.length != 0) {
                  updateSettings();
                }
              }}>X</div>
    </div>
  </PopoutWrapper>);
  }
  const chooseClick = (name) => {
    var element = document.getElementById(name);
    switch (element.getAttribute('class')){
        case 'CardCategorySelectedFirstConnect': 
            element.setAttribute('class', 'CardCategoryUnselectedFirstConnect');
            selected.splice(selected.indexOf(name),1);
        break;
        case 'CardCategoryUnselectedFirstConnect': 
            element.setAttribute('class', 'CardCategorySelectedFirstConnect');
            selected.push(name);
        break;
    }
    // console.log(selected);
    document.getElementById("buttonGo").setAttribute('style',selected.length != 0?"background : #4bb34b":"background : #e64646")
  }
// const [data, setData] = useState(null);
  useEffect(() => {

  }, []);
  // console.log(props.data);
  // console.log(props.data.categori.indexOf("Доставка"));
  return(
	  <Panel id="Profile">
            <PanelHeader>Профиль</PanelHeader>

            <div className="VkDataBlockProfile">
              <img className="AvatarProfile" src={props.fetchedUser.photo_200}></img> 
              <div className="FIOProfile">{props.fetchedUser.last_name} {props.fetchedUser.first_name}</div>
              <div className="UnderButtonsProfile">
                <Button className="ButtonProfile" mode="commerce" onClick={()=>{
                    window.open(`https://vk.com/${props.data.vk_id}`)
                }}>VK Профиль</Button>
                <Button className="ButtonProfile" mode="destructive" onClick={()=>{alert("Пока в разработке")}}>Пожаловаться</Button>
              </div>
              <Separator />
              <div className="CategoryProfile">
                {props.data && props.data.categori.map((category,index) => {
                    return(<div className="CardCategoryProfile" key={index}>{category}</div>)
                })

                }
                <div className="ButtonAddProfile" onClick={()=>{openSettings();}}>
                  +
                </div>
              </div>
              {/* {/* <Avatar className="AvatarProfile" src={props.fetchedUser.photo_200}/> */}
            </div>
      <Separator />
        <Header  indicator={<Counter size="s" mode="prominent">{props.data.feedback.length}</Counter>}>
        Отзывы
      </Header>
      { (props.data.feedback.length != 0)?props.data.feedback.map((item,index) =>{
          return <div className="feedbackBodyProfile" key={index}>
                    <Card mode="shadow" key={index} className="feedbackCardProfile">
                        {item}
                    </Card>
                  </div> 
      }):(
          <Footer>0 отзывов</Footer>
        )

      }

    </Panel>
        
);
}
Profile.propTypes = {
};

export default Profile;
